/**
 * Mixins
 * Reusable SCSS mixins for Aqua-style effects
 */
/**
 * Aqua Glass Effect (consolidated)
 * Creates the signature Mac OS X Aqua glossy appearance with:
 * - Top highlight/shine (::before pseudo-element)
 * - Bottom glow reflection (::after pseudo-element)
 *
 * Core presets:
 *   'round'  - Circular/spherical elements
 *   'box'    - Rectangular controls and buttons
 *   'bar'    - Flat/full-width bars and headers
 *
 * Modifier aliases:
 *   'subtle' - Lighter shine-only effect
 *
 * Backwards-compatible aliases are supported (e.g. 'circle', 'pill', 'header', ...).
 */
/* ============================================
   Presets + aliases (single source of truth)
   ============================================ */
/* ============================================
   Internal helpers
   ============================================ */
/* ============================================
   Public API
   ============================================ */
/* ============================================
   Backwards-compatible wrappers
   ============================================ */
/**
 * Base Styles
 * Global resets and typography
 */
html {
  overscroll-behavior: none;
}

body {
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  color: var(--text-color);
  background: var(--pinstripe);
  background-color: #FFFFFF;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overscroll-behavior: none;
}

* {
  box-sizing: border-box;
}

a {
  color: var(--aqua-blue-dark);
}
a:hover {
  color: var(--aqua-blue);
}

/**
 * Backgrounds & Pinstripes
 * Mac OS X Aqua-style background patterns and utility classes
 */
/* CSS Variables for Aqua theming */
:root {
  /* Pinstripe patterns */
  --aqua-pinstripe-light: repeating-linear-gradient(
    0deg,
    transparent 0px,
    transparent 1.5px,
    rgba(255, 255, 255, 0.85) 1.5px,
    rgba(255, 255, 255, 0.85) 4px
  );
  --aqua-pinstripe-window: repeating-linear-gradient(
      0deg,
      transparent 0px,
      transparent 1.5px,
      rgba(255, 255, 255, 0.85) 1.5px,
      rgba(255, 255, 255, 0.85) 4px
    ),
    linear-gradient(to bottom, #ececec 0%, #ececec 100%);
  --aqua-pinstripe-titlebar: linear-gradient(
    to bottom,
    rgba(255, 255, 255, 0.3) 0%,
    rgba(219, 219, 219, 0.6) 70%,
    rgba(206, 206, 206, 0.7) 100%
  );
  --aqua-pinstripe-menubar: repeating-linear-gradient(
    0deg,
    transparent 0px,
    transparent 1.5px,
    rgba(255, 255, 255, 0.55) 1.5px,
    rgba(255, 255, 255, 0.55) 4px
  );
  --aqua-pinstripe-diagonal: repeating-linear-gradient(
    -45deg,
    #eee 0px,
    #eee 2px,
    #e3e3e3 2px,
    #e3e3e3 4px
  );
  /* Brushed metal texture */
  --aqua-brushed-metal: linear-gradient(
    to bottom,
    #e8e8e8 0%,
    #d0d0d0 50%,
    #c8c8c8 100%
  );
  /* Selection colors */
  --aqua-selection-bg: rgba(39, 101, 202, 0.88);
  --aqua-selection-text: #ffffff;
  --aqua-selection-glow: rgba(48, 103, 218, 0.5);
  /* Primary blue gradient (for buttons, progress bars) */
  --aqua-blue-gradient: linear-gradient(
    rgba(0, 65, 184, 0.625),
    rgba(45, 115, 199, 0.625),
    rgba(33, 160, 196, 0.625)
  );
  /* Secondary gray gradient */
  --aqua-gray-gradient: linear-gradient(
    rgba(160, 160, 160, 0.625),
    rgba(255, 255, 255, 0.625)
  );
  /* Window backgrounds */
  --aqua-window-bg: #ececec;
  --aqua-titlebar-bg: linear-gradient(to bottom, #e8e8e8 0%, #d0d0d0 100%);
  --aqua-titlebar-inactive-bg: linear-gradient(to bottom, #f6f6f6 0%, #e0e0e0 100%);
  /* Border colors */
  --aqua-border-color: rgba(0, 0, 0, 0.3);
  --aqua-border-color-light: rgba(0, 0, 0, 0.15);
  /* Focus ring */
  --focus-outer: rgba(52, 106, 227, 0.45);
  --focus-inner: rgba(180, 210, 255, 0.8);
  --aqua-focus-ring: 0 0 0 3px var(--focus-outer), 0 0 0 1px var(--focus-inner);
  /* Shadows */
  --aqua-window-shadow: 0 8px 25px rgba(0, 0, 0, 0.4), 0 2px 8px rgba(0, 0, 0, 0.2);
  --aqua-window-shadow-inactive: 0 4px 15px rgba(0, 0, 0, 0.2), 0 1px 4px rgba(0, 0, 0, 0.1);
  --aqua-button-shadow: 0 2px 3px rgba(0, 0, 0, 0.3), 0 1px 1px rgba(0, 78, 187, 0.5);
}

/* Classic horizontal stripes (like Finder windows) */
.aqua-background {
  background: repeating-linear-gradient(#F0F0F0, #F0F0F0 4px, #FFFFFF 4px, #FFFFFF 8px);
}

/* Diagonal stripes */
.aqua-background--diagonal {
  background: repeating-linear-gradient(45deg, #eee, #eee 10px, #e3e3e3 10px, #e3e3e3 20px);
}

/* Solid gray */
.aqua-background--gray {
  background: #e8e8e8;
}

/* Brushed metal */
.aqua-background--metal {
  background: linear-gradient(to bottom, #e8e8e8 0%, #d0d0d0 50%, #c8c8c8 100%);
}

/* Full viewport wrapper */
.aqua-background--full {
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
  box-sizing: border-box;
}

/* Utility classes for backgrounds */
.aqua-bg-window {
  background-image: var(--aqua-pinstripe-window);
}

.aqua-bg-pinstripe {
  background-image: var(--aqua-pinstripe-light);
  background-color: #ececec;
}

.aqua-bg-pinstripe-diagonal {
  background-image: var(--aqua-pinstripe-diagonal);
}

.aqua-bg-titlebar {
  background-image: var(--aqua-pinstripe-titlebar), var(--aqua-titlebar-bg);
}

.aqua-bg-menubar {
  background-image: var(--aqua-pinstripe-menubar), linear-gradient(to bottom, #f8f8f8 0%, #e8e8e8 50%, #d4d4d4 100%);
}

.aqua-bg-brushed-metal {
  background: var(--aqua-brushed-metal);
}

.aqua-bg-solid {
  background: var(--aqua-window-bg);
}

/* Utility classes for borders */
.aqua-border {
  border: 1px solid var(--aqua-border-color);
}

.aqua-border-light {
  border: 1px solid var(--aqua-border-color-light);
}

.aqua-border-bottom {
  border-bottom: 1px solid var(--aqua-border-color);
}

/* Utility classes for shadows */
.aqua-shadow-window {
  box-shadow: var(--aqua-window-shadow);
}

.aqua-shadow-window-inactive {
  box-shadow: var(--aqua-window-shadow-inactive);
}

.aqua-shadow-button {
  box-shadow: var(--aqua-button-shadow);
}

/* Focus ring utility */
.aqua-focus-ring:focus {
  outline: none;
  box-shadow: var(--aqua-focus-ring);
}

.aqua-focus-ring.is-focused {
  outline: none;
  box-shadow: var(--aqua-focus-ring);
}

/* Selection color utility */
.aqua-selection::selection {
  background: var(--aqua-selection-bg);
  color: var(--aqua-selection-text);
}

/* Gloss effect utility (for buttons, bars) */
.aqua-gloss {
  position: relative;
}

.aqua-gloss::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 50%;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0.3) 0%, rgba(255, 255, 255, 0.1) 100%);
  pointer-events: none;
  z-index: 1;
}

/* Inset utility for inputs */
.aqua-inset {
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.15), inset 0 0 0 1px rgba(0, 0, 0, 0.2), 0 1px 0 rgba(255, 255, 255, 0.8);
}

/* Text shadows */
.aqua-text-shadow {
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
}

.aqua-text-shadow-dark {
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.3);
}

/* Font family utility */
.aqua-font {
  font-family: "Lucida Grande", "Lucida Sans Unicode", sans-serif;
  -webkit-font-smoothing: antialiased;
}

/* Rounded corners utilities */
.aqua-rounded {
  border-radius: 8px;
}

.aqua-rounded-top {
  border-radius: 8px 8px 0 0;
}

.aqua-rounded-pill {
  border-radius: 9999px;
}

/* Disabled state utility */
.aqua-disabled {
  opacity: 0.5;
  cursor: not-allowed;
  pointer-events: none;
}

/* Core Components */
/**
 * Window
 * Mac OS X Aqua-style window with titlebar and traffic light controls
 */
.window {
  display: flex;
  flex-direction: column;
  border-radius: 8px 8px 0 0;
  overflow: hidden;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  /* Window border */
  border: 1px solid rgba(0, 0, 0, 0.4);
  /* Window shadow */
  box-shadow: 0 8px 25px rgba(0, 0, 0, 0.4), 0 2px 8px rgba(0, 0, 0, 0.2);
}
.window.inactive {
  border-color: rgba(0, 0, 0, 0.2);
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2), 0 1px 4px rgba(0, 0, 0, 0.1);
}
.window.inactive .title-bar {
  background: linear-gradient(to bottom, #f6f6f6 0%, #e0e0e0 100%);
  border-bottom-color: rgba(0, 0, 0, 0.2);
}
.window.inactive .title-bar-controls button {
  background: linear-gradient(rgba(160, 160, 160, 0.625), rgba(200, 200, 200, 0.625), rgba(180, 180, 180, 0.625)) !important;
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(0, 0, 0, 0.2), inset 0 0.125em 0.2em rgba(0, 0, 0, 0.4), inset 0 0.15em 0.25em 0.1em rgba(150, 150, 150, 0.75) !important;
}
.window.inactive .title-bar-text {
  color: #7f7f7f;
}
.window.brushed-metal .window-body {
  background: linear-gradient(to bottom, #e8e8e8 0%, #d0d0d0 50%, #c8c8c8 100%);
  background-image: none;
}

.window-body {
  flex: 1;
  padding: 16px;
  overflow: auto;
  background: #ececec;
}

/* Title Bar */
.title-bar {
  display: flex;
  align-items: center;
  height: 22px;
  padding: 0 8px;
  gap: 8px;
  /* Gradient background with pinstripes */
  background: linear-gradient(to bottom, #e8e8e8 0%, #d0d0d0 100%);
  background-image: linear-gradient(to bottom, rgba(255, 255, 255, 0.3) 0%, rgba(219, 219, 219, 0.6) 70%, rgba(206, 206, 206, 0.7) 100%), linear-gradient(to bottom, #e8e8e8 0%, #d0d0d0 100%);
  border-bottom: 1px solid rgba(0, 0, 0, 0.3);
}

.title-bar-text {
  flex: 1;
  text-align: center;
  font-size: var(--font-size-title);
  font-weight: 500;
  color: #000;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  user-select: none;
}

/* Spacer to center title */
.title-bar-spacer {
  width: 54px;
}

/* Traffic Light Controls */
.title-bar-controls {
  display: flex;
  gap: 8px;
}
.title-bar-controls:hover button {
  /* Show icons on hover */
}
.title-bar-controls:hover button .icon {
  opacity: 1;
}
.title-bar-controls button {
  position: relative;
  width: 14px;
  height: 14px;
  border-radius: 50%;
  border: none;
  padding: 0;
  min-width: 0;
  min-height: 0;
  cursor: pointer;
  overflow: visible;
  transition: all 0.15s ease;
  background: none;
  font-size: 14px;
  /* Icon inside button */
}
.title-bar-controls button .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  font-size: 8px;
  font-weight: bold;
  line-height: 1;
  opacity: 0;
  z-index: 10;
  transition: opacity 0.1s ease;
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
}
.title-bar-controls button {
  /* Aqua glass effect (top shine + bottom glow) */
}
.title-bar-controls button::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.title-bar-controls button::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}
.title-bar-controls button {
  /* Close button - Red */
}
.title-bar-controls button[aria-label=Close] {
  background: linear-gradient(rgb(193, 58, 45), rgb(205, 73, 52));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(150, 40, 30, 0.5), inset 0 0.125em 0.2em rgba(150, 40, 30, 0.8), inset 0 0.15em 0.25em 0.1em rgba(225, 70, 64, 0.75);
}
.title-bar-controls button[aria-label=Close] .icon {
  color: #4d0f0a;
  text-shadow: 0 1px 0 rgba(255, 150, 150, 0.3);
}
.title-bar-controls button[aria-label=Close]:hover {
  background: linear-gradient(rgb(213, 78, 65), rgb(225, 93, 72));
}
.title-bar-controls button {
  /* Minimize button - Yellow */
}
.title-bar-controls button[aria-label=Minimize] {
  background: linear-gradient(rgb(202, 130, 13), rgb(253, 253, 149));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(155, 78, 21, 0.5), inset 0 0.125em 0.2em rgba(155, 78, 21, 0.8), inset 0 0.15em 0.25em 0.1em rgba(241, 157, 20, 0.75);
}
.title-bar-controls button[aria-label=Minimize] .icon {
  color: #5a4005;
  text-shadow: 0 1px 0 rgba(255, 220, 150, 0.3);
}
.title-bar-controls button[aria-label=Minimize]:hover {
  background: linear-gradient(rgb(222, 150, 33), rgb(255, 255, 169));
}
.title-bar-controls button {
  /* Zoom/Maximize button - Green */
}
.title-bar-controls button[aria-label=Maximize], .title-bar-controls button[aria-label=Zoom] {
  background: linear-gradient(rgb(111, 174, 58), rgb(138, 192, 50));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(53, 91, 17, 0.5), inset 0 0.125em 0.2em rgba(53, 91, 17, 0.8), inset 0 0.15em 0.25em 0.1em rgba(98, 187, 19, 0.75);
}
.title-bar-controls button[aria-label=Maximize] .icon, .title-bar-controls button[aria-label=Zoom] .icon {
  color: #0a4d12;
  text-shadow: 0 1px 0 rgba(150, 255, 150, 0.3);
}
.title-bar-controls button[aria-label=Maximize]:hover, .title-bar-controls button[aria-label=Zoom]:hover {
  background: linear-gradient(rgb(131, 194, 78), rgb(158, 212, 70));
}
.title-bar-controls button:active:not(:disabled) {
  filter: brightness(0.85);
}

/* Toolbar */
.toolbar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 8px;
  min-height: 32px;
  /* Brushed metal texture */
  background: linear-gradient(to bottom, #e8e8e8 0%, #d0d0d0 50%, #c8c8c8 100%);
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5), inset 0 -1px 0 rgba(0, 0, 0, 0.1);
}

/* Status Bar */
.status-bar {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 4px 8px;
  min-height: 20px;
  font-size: var(--font-size-small);
  color: #666;
  background: linear-gradient(to bottom, #e0e0e0 0%, #d8d8d8 100%);
  border-top: 1px solid rgba(0, 0, 0, 0.15);
  box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.5);
}

/* Sheet variant (modal attached to window) */
.window.sheet {
  border-radius: 0 0 8px 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.35);
}
.window.sheet .title-bar {
  display: none;
}

/* Utility window variant (smaller, no pinstripes) */
.window.utility {
  border-radius: 4px 4px 0 0;
}
.window.utility .title-bar {
  height: 16px;
  padding: 0 4px;
  gap: 4px;
}
.window.utility .title-bar-controls button {
  width: 10px;
  height: 10px;
}
.window.utility .title-bar-text {
  font-size: 11px;
}
.window.utility .title-bar-spacer {
  width: 38px;
}
.window.utility .window-body {
  background: #ececec;
  background-image: none;
}

/**
 * Traffic Lights
 * Mac OS X Aqua-style window control buttons (close, minimize, zoom)
 */
/* Traffic Lights Container */
.traffic-lights {
  display: flex;
  gap: 8px;
  align-items: center;
}

/* Base Aqua Light Style */
.aqua-light {
  --button-size: 14px;
  position: relative;
  width: var(--button-size);
  height: var(--button-size);
  font-size: var(--button-size);
  border-radius: 50%;
  border: none;
  padding: 0;
  cursor: pointer;
  transition: all 0.15s ease;
  overflow: hidden;
  /* Gray/inactive state by default */
  background: linear-gradient(rgba(160, 160, 160, 0.625), rgba(200, 200, 200, 0.625), rgba(180, 180, 180, 0.625));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(0, 0, 0, 0.2), inset 0 0.125em 0.2em rgba(0, 0, 0, 0.4), inset 0 0.15em 0.25em 0.1em rgba(150, 150, 150, 0.75);
  /* Aqua glass effect (top shine + bottom glow) */
}
.aqua-light::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.aqua-light::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}

/* Icon styles - hidden by default */
.aqua-light .icon {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -55%);
  font-size: 1em;
  font-weight: bold;
  line-height: 1;
  opacity: 0;
  z-index: 10;
  transition: opacity 0.1s ease;
}

.aqua-red .icon {
  color: #4d0f0a;
  text-shadow: 0 1px 0 rgba(255, 150, 150, 0.3);
}

.aqua-yellow .icon {
  color: #5a4005;
  text-shadow: 0 1px 0 rgba(255, 220, 150, 0.3);
}

.aqua-green .icon {
  color: #0a4d12;
  text-shadow: 0 1px 0 rgba(150, 255, 150, 0.3);
}

/* Show ALL icons when hovering over the container */
.traffic-lights:hover .aqua-light:not(.aqua-inactive) .icon {
  opacity: 1;
}

/* Close - Red (on hover) */
.traffic-lights:hover .aqua-red:not(.aqua-inactive) {
  background: linear-gradient(rgb(193, 58, 45), rgb(205, 73, 52));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(150, 40, 30, 0.5), inset 0 0.125em 0.2em rgba(150, 40, 30, 0.8), inset 0 0.15em 0.25em 0.1em rgba(225, 70, 64, 0.75);
}

/* Minimize - Yellow/Orange (on hover) */
.traffic-lights:hover .aqua-yellow:not(.aqua-inactive) {
  background: linear-gradient(rgb(202, 130, 13), rgb(253, 253, 149));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(155, 78, 21, 0.5), inset 0 0.125em 0.2em rgba(155, 78, 21, 0.8), inset 0 0.15em 0.25em 0.1em rgba(241, 157, 20, 0.75);
}

/* Maximize - Green (on hover) */
.traffic-lights:hover .aqua-green:not(.aqua-inactive) {
  background: linear-gradient(rgb(111, 174, 58), rgb(138, 192, 50));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(53, 91, 17, 0.5), inset 0 0.125em 0.2em rgba(53, 91, 17, 0.8), inset 0 0.15em 0.25em 0.1em rgba(98, 187, 19, 0.75);
}

/* Permanently inactive */
.aqua-inactive {
  background: linear-gradient(rgba(160, 160, 160, 0.625), rgba(200, 200, 200, 0.625), rgba(180, 180, 180, 0.625)) !important;
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(0, 0, 0, 0.2), inset 0 0.125em 0.2em rgba(0, 0, 0, 0.4), inset 0 0.15em 0.25em 0.1em rgba(150, 150, 150, 0.75) !important;
  cursor: default;
}

/* Pressed state */
.traffic-lights:hover .aqua-light:active {
  filter: brightness(0.85);
  transition: none;
}

/* Size variations */
.traffic-lights--large .aqua-light {
  --button-size: 32px;
}

.traffic-lights--small .aqua-light {
  --button-size: 10px;
}

/* Always show colors (for demo/static display) */
.traffic-lights--always-colored .aqua-red:not(.aqua-inactive) {
  background: linear-gradient(rgb(193, 58, 45), rgb(205, 73, 52));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(150, 40, 30, 0.5), inset 0 0.125em 0.2em rgba(150, 40, 30, 0.8), inset 0 0.15em 0.25em 0.1em rgba(225, 70, 64, 0.75);
}

.traffic-lights--always-colored .aqua-yellow:not(.aqua-inactive) {
  background: linear-gradient(rgb(202, 130, 13), rgb(253, 253, 149));
  box-shadow: 0 0.125em 0.2em rgba(0, 0, 0, 0.3), 0 0.0625em 0.0625em rgba(155, 78, 21, 0.5), inset 0 0.125em 0.2em rgba(155, 78, 21, 0.8), inset 0 0.15em 0.25em 0.1em rgba(241, 157, 20, 0.75);
}

.traffic-lights--always-colored .aqua-green:not(.aqua-inactive) {
