}

/* Unsaved document indicator (dot in close button) */
.aqua-red.unsaved .icon {
  display: none;
}

.aqua-red.unsaved::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 5px;
