.aqua-slider,
.slider {
  /* Mozilla thumb (Firefox) */
}
input[type=range]::-moz-range-thumb,
.aqua-slider::-moz-range-thumb,
.slider::-moz-range-thumb {
  width: 17px;
  height: 17px;
  border: 0.5px solid rgba(87, 87, 87, 0.8);
  border-radius: 50%;
  background: linear-gradient(rgb(255, 255, 255) 0%, rgb(252, 252, 252) 25%, rgb(244, 244, 244) 50%, rgb(238, 238, 238) 51%, rgb(243, 243, 243) 95%, rgb(255, 255, 255) 97.5%, rgb(243, 243, 243) 100%);
  cursor: pointer;
  box-shadow: 0 0 2px rgba(192, 192, 198, 0.5);
}
input[type=range]::-moz-range-thumb:active,
.aqua-slider::-moz-range-thumb:active,
.slider::-moz-range-thumb:active {
  background: linear-gradient(rgb(224, 224, 224) 0%, rgb(214, 214, 214) 50%, rgb(207, 207, 207) 51%, rgb(208, 208, 208) 95%, rgb(220, 220, 220) 97.5%, rgb(208, 208, 208) 100%);
}
input[type=range],
.aqua-slider,
.slider {
  /* Mozilla track */
}
input[type=range]::-moz-range-track,
.aqua-slider::-moz-range-track,
.slider::-moz-range-track {
  height: 4px;
  background: linear-gradient(to bottom, rgb(147, 147, 147), rgb(217, 217, 217));
  border: 0.5px solid;
  border-color: rgb(111, 111, 111) rgb(171, 171, 171) rgb(171, 171, 171) rgb(111, 111, 111);
  border-radius: 2.5px;
}
input[type=range],
.aqua-slider,
.slider {
  /* Focus state */
}
input[type=range]:focus::-webkit-slider-thumb,
.aqua-slider:focus::-webkit-slider-thumb,
.slider:focus::-webkit-slider-thumb {
  box-shadow: 0 0 0 3px rgba(104, 155, 201, 0.5);
