.aqua-slider:focus::-moz-range-thumb,
.slider:focus::-moz-range-thumb {
  box-shadow: 0 0 0 3px rgba(104, 155, 201, 0.5);
}
input[type=range]:disabled,
.aqua-slider:disabled,
.slider:disabled {
  opacity: 0.5;
  pointer-events: none;
  cursor: not-allowed;
}
input[type=range]:disabled::-webkit-slider-thumb,
.aqua-slider:disabled::-webkit-slider-thumb,
.slider:disabled::-webkit-slider-thumb {
  cursor: not-allowed;
}
input[type=range]:disabled::-moz-range-thumb,
.aqua-slider:disabled::-moz-range-thumb,
.slider:disabled::-moz-range-thumb {
  cursor: not-allowed;
}

/* Vertical slider */
input[type=range].vertical,
