/**
 * Switch / Toggle
 * Mac OS X Aqua-style toggle switch
 */
.aqua-switch {
  position: relative;
  border-radius: 9999px;
  background: linear-gradient(#d0d0d0, #f5f5f5);
  border: 1px solid rgba(0, 0, 0, 0.18);
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.12), 0 1px 0 rgba(255, 255, 255, 0.55);
  padding: 0 2px;
  height: 16px;
  width: 28px;
  transition: background 200ms, box-shadow 200ms;
  cursor: pointer;
  display: inline-flex;
  align-items: center;
}
.aqua-switch:focus {
  outline: none;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.12), 0 1px 0 rgba(255, 255, 255, 0.55), 0 0 0 2px rgba(48, 103, 218, 0.3);
}

/* Thumb base shape */
.aqua-switch-thumb {
  position: relative;
  height: 14px;
  width: 14px;
  border-radius: 9999px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border: none;
  padding: 0;
  box-sizing: border-box;
  overflow: hidden;
  transition: transform 200ms ease, background 200ms;
  transform: translateX(0);
  /* Aqua glass effect (top shine + bottom glow) */
}
.aqua-switch-thumb::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 40%;
  width: 75%;
  top: 8%;
  background: linear-gradient(rgba(255, 255, 255, 0.8), rgba(255, 255, 255, 0.15));
  border-radius: 50% 50% 30% 30%;
  filter: blur(0.5px);
  z-index: 2;
  pointer-events: none;
}
.aqua-switch-thumb::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 35%;
  width: 70%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.35));
  border-radius: 30% 30% 50% 50%;
  filter: blur(1px);
  pointer-events: none;
}

/* OFF state - gray gradient */
.aqua-switch:not(.aqua-switch--checked) .aqua-switch-thumb {
  background: linear-gradient(rgba(160, 160, 160, 0.625), rgba(255, 255, 255, 0.625));
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2), 0 1px 1px rgba(0, 0, 0, 0.3), inset 0 1px 2px rgba(0, 0, 0, 0.4), inset 0 2px 3px 1px #bbbbbb;
}

/* ON state - blue gradient */
.aqua-switch--checked .aqua-switch-thumb {
  background: linear-gradient(rgba(0, 65, 184, 0.625), rgba(45, 115, 199, 0.625), rgba(33, 160, 196, 0.625));
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.3), 0 1px 1px rgba(0, 78, 187, 0.5), inset 0 1px 3px rgba(0, 17, 49, 0.8), inset 0 2px 3px 1px rgba(0, 78, 187, 0.75);
  transform: translateX(10px);
}

/* Size variations */
.aqua-switch--small {
  height: 12px;
  width: 22px;
}
.aqua-switch--small .aqua-switch-thumb {
  height: 10px;
  width: 10px;
}
.aqua-switch--small.aqua-switch--checked .aqua-switch-thumb {
  transform: translateX(8px);
}

.aqua-switch--large {
  height: 22px;
  width: 38px;
  padding: 0 3px;
}
.aqua-switch--large .aqua-switch-thumb {
  height: 18px;
  width: 18px;
}
.aqua-switch--large.aqua-switch--checked .aqua-switch-thumb {
  transform: translateX(14px);
}

/* Disabled state */
