/**
 * Progress Bar
 * Mac OS X Aqua-style progress bars - JavaFX AquaFX accurate
 * Includes: Linear bar, Indeterminate, Circular indicator, Spinner
 */
/* ============================================
   LINEAR PROGRESS BAR
   ============================================ */
.progress-bar,
.aqua-progress {
  width: 200px;
  height: 13px;
  border: 0.5px solid rgb(146, 146, 146);
  border-radius: 2.5px;
  background: linear-gradient(rgb(176, 176, 176), rgb(207, 207, 207)), linear-gradient(rgb(190, 190, 190) 0%, rgb(213, 213, 213) 15%, rgb(230, 230, 230) 50%, rgb(235, 235, 235) 100%);
  overflow: hidden;
  position: relative;
}

.progress-bar-fill {
  height: 100%;
  background: linear-gradient(to right, rgb(84, 170, 240), rgb(90, 192, 246));
  border-right: 0.5px solid rgb(161, 161, 161);
  border-radius: 2px;
  transition: width 300ms ease;
}

/* aqua-progress inner bar (uses progress.png pattern like the showcase) */
.aqua-progress .bar {
  height: 100%;
  border-right: 0.5px solid rgb(161, 161, 161);
  border-radius: 2px;
  transition: width 300ms ease;
  background-image: url("progress.png");
  background-repeat: repeat-x;
  background-size: auto 100%;
  animation: aqua-progress-scroll 1s linear infinite;
}

/* Animated scrolling pattern */
.progress-bar.animated .progress-bar-fill {
  background-image: repeating-linear-gradient(-45deg, rgb(84, 170, 240), rgb(84, 170, 240) 5px, rgb(110, 190, 250) 5px, rgb(110, 190, 250) 10px);
  background-size: 14px 100%;
  animation: progress-scroll 1s linear infinite;
}

@keyframes progress-scroll {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -14px 0;
  }
}
@keyframes aqua-progress-scroll {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -38px 0;
  }
}
/* Indeterminate state - candy cane stripes */
.progress-bar.indeterminate,
.aqua-progress.indeterminate {
  position: relative;
  background: repeating-linear-gradient(135deg, rgb(84, 169, 239) 0px, rgb(84, 169, 239) 5px, rgb(236, 236, 236) 5px, rgb(236, 236, 236) 10px), linear-gradient(rgb(176, 176, 176), rgb(207, 207, 207));
  background-size: 14px 14px, 100% 100%;
  animation: progress-indeterminate 2s linear infinite;
  border-radius: 2.5px;
  /* Aqua glass effect (top shine + bottom glow) */
}
.progress-bar.indeterminate::before,
.aqua-progress.indeterminate::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 50%;
  width: 100%;
  top: 0;
  background: linear-gradient(rgba(255, 255, 255, 0.4), rgba(255, 255, 255, 0.05));
  border-radius: 0;
  z-index: 1;
  pointer-events: none;
}
.progress-bar.indeterminate::after,
.aqua-progress.indeterminate::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 30%;
  width: 100%;
  bottom: 0;
  background: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0.2));
  border-radius: 0;
  z-index: 1;
  pointer-events: none;
}
.progress-bar.indeterminate .progress-bar-fill,
.aqua-progress.indeterminate .progress-bar-fill {
  display: none;
}
.progress-bar.indeterminate .bar,
.aqua-progress.indeterminate .bar {
  display: none;
}

@keyframes progress-indeterminate {
  from {
    background-position: 0 0, 0 0;
  }
  to {
    background-position: 200px 0, 0 0;
  }
}
/* Size variations */
.progress-bar.small,
.aqua-progress.small {
  height: 8px;
}

.progress-bar.large,
.aqua-progress.large {
  height: 18px;
}

/* Disabled state */
.progress-bar.disabled,
.aqua-progress.disabled {
  opacity: 0.6;
  pointer-events: none;
}

/* ============================================
   CIRCULAR PROGRESS INDICATOR (Determinate)
   Uses --progress as a number 0-100
   ============================================ */
.progress-indicator,
.aqua-progress-indicator {
  width: 32px;
  height: 32px;
  position: relative;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  /* Outer ring (track) */
}
.progress-indicator .indicator,
.aqua-progress-indicator .indicator {
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: linear-gradient(to bottom, rgb(190, 190, 190) 0%, rgb(213, 213, 213) 15%, rgb(230, 230, 230) 50%, rgb(235, 235, 235) 100%);
