.progress-indicator,
.aqua-progress-indicator {
  /* Progress fill (pie style using conic-gradient) */
}
.progress-indicator .progress,
.aqua-progress-indicator .progress {
  position: absolute;
  width: calc(100% - 4px);
  height: calc(100% - 4px);
  border-radius: 50%;
  background: conic-gradient(from -90deg, rgb(90, 192, 246) 0deg, rgb(84, 170, 240) calc(var(--progress, 0) * 3.6deg), transparent calc(var(--progress, 0) * 3.6deg), transparent 360deg);
  border: 0.5px solid rgb(208, 208, 208);
  /* Inner hole to make it a ring */
}
.progress-indicator .progress::before,
.aqua-progress-indicator .progress::before {
  content: "";
  position: absolute;
  top: 2px;
  left: 2px;
  right: 2px;
  bottom: 2px;
  background: linear-gradient(to bottom, rgb(250, 250, 250), rgb(255, 255, 255));
  border-radius: 50%;
}
.progress-indicator,
.aqua-progress-indicator {
