/* ============================================
   SPINNER INDICATOR (Indeterminate - 12 segments)
   ============================================ */
.spinner-indicator,
.aqua-spinner-indicator {
  width: 20px;
  height: 20px;
  position: relative;
  animation: spinner-rotate 1s steps(12) infinite;
  /* Each segment is positioned around center */
}
.spinner-indicator .segment,
.aqua-spinner-indicator .segment {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  /* The actual segment shape */
}
.spinner-indicator .segment::before,
.aqua-spinner-indicator .segment::before {
  content: "";
  position: absolute;
  width: 2px;
  height: 5px;
  background: rgb(95, 95, 98);
  border-radius: 1px;
  left: 50%;
  top: 0;
  margin-left: -1px;
  box-shadow: inset 0 0 0 0.5px rgb(122, 122, 125);
}
.spinner-indicator,
.aqua-spinner-indicator {
  /* Position each segment with rotation */
}
.spinner-indicator .segment:nth-child(1),
.aqua-spinner-indicator .segment:nth-child(1) {
  transform: rotate(0deg);
}
.spinner-indicator .segment:nth-child(2),
.aqua-spinner-indicator .segment:nth-child(2) {
