/**
 * Tabs
 * Mac OS X Aqua-style tab list and tab panels with glossy gradients
 */
/* Tab bar mixin for shared styles */
/* Tab item mixin for shared styles */
/* ===== Aqua Tab Classes (Recommended) ===== */
.aqua-tab-bar {
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: flex-start;
  gap: 2px;
  padding-bottom: 0;
  background: transparent;
  /* Colorful gradient bar at the bottom */
}
.aqua-tab-bar::after {
  content: "";
  position: absolute;
  bottom: -5px;
  left: 0;
  right: 0;
  height: 6px;
  z-index: 3;
  background: linear-gradient(to bottom, #2260a4 0%, #e2fbfd 8%, #aecef3 25%, #7aafee 40%, #98ceff 60%, #9dcfff 80%, #74deff 100%);
  box-shadow: inset 0 1px 0 rgba(0, 0, 0, 0.3), inset 0 -1px 0 rgba(0, 0, 0, 0.3);
}

.aqua-tab {
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.3);
  border-radius: 6px 6px 0 0;
  transition: all 150ms;
  overflow: hidden;
  height: 28px;
  padding: 0 16px;
  margin-top: 4px;
  cursor: default;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  font-family: "Lucida Grande", "Helvetica Neue", sans-serif;
  font-size: 12px;
  white-space: nowrap;
  /* Inactive tab (secondary aqua button style) */
  background: linear-gradient(rgb(197, 197, 197), rgb(255, 255, 255));
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1), inset 0 2px 3px 1px rgba(187, 187, 187, 0.8), 0 1px 1px rgba(0, 0, 0, 0.3);
  color: black;
  text-shadow: 0 2px 2px rgba(0, 0, 0, 0.25);
  /* Aqua glass effect (top shine + bottom glow) */
}
.aqua-tab::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  top: 5%;
  background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.3));
  border-radius: inherit;
  filter: blur(1px);
  z-index: 1;
  pointer-events: none;
}
.aqua-tab::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5));
  border-radius: inherit;
  filter: blur(3px);
  z-index: 1;
  pointer-events: none;
}
.aqua-tab:hover:not(.aqua-tab--active) {
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.05), inset 0 2px 3px 1px rgba(187, 187, 187, 0.6), 0 1px 1px rgba(0, 0, 0, 0.2);
}
.aqua-tab.aqua-tab--active {
  margin-top: 0;
  z-index: 10;
  /* Active tab (primary aqua button style) */
  background: linear-gradient(rgb(48, 123, 201), rgb(152, 189, 228), rgb(210, 255, 255));
  box-shadow: inset 0 1px 3px rgba(0, 17, 49, 0.8), inset 0 2px 3px 1px rgba(0, 78, 187, 0.75), 0 2px 3px rgba(0, 0, 0, 0.3), 0 1px 1px rgba(0, 78, 187, 0.5);
  text-shadow: 0 2px 2px rgba(30, 77, 161, 0.5);
