  box-shadow: 0 0.8px 1px rgba(172, 172, 184, 0.5);
}
.toolbar-button:hover {
  background: linear-gradient(rgb(250, 250, 250), rgb(195, 195, 195));
}
.toolbar-button:active {
  background: linear-gradient(rgb(160, 160, 160), rgb(215, 215, 215));
}
.toolbar-button:disabled {
  opacity: 0.6;
  cursor: default;
  pointer-events: none;
}
.toolbar-button {
  /* With icon */
}
.toolbar-button.with-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2px;
  padding: 4px 8px;
  min-width: 48px;
}
.toolbar-button.with-icon img,
.toolbar-button.with-icon .icon {
  width: 24px;
  height: 24px;
}
.toolbar-button.with-icon span {
  font-size: var(--font-size-small);
}

/* Toolbar separator */
.toolbar-separator {
  width: 1px;
  height: 20px;
  border-right: 1px solid rgb(129, 129, 129);
  margin: 0 1px;
}

/* Toolbar spacer (flexible) */
.toolbar-spacer {
  flex: 1;
}

/* Toolbar search field */
.toolbar-search {
  background: #fff;
  border: 1px solid rgb(180, 180, 180);
  border-radius: 10px;
  padding: 2px 8px;
