.segmented-control {
  display: inline-flex;
  border: 1px solid #888888;
  border-radius: var(--radius-medium);
  overflow: hidden;
}
.segmented-control button {
  border: none;
  border-radius: 0;
  border-right: 1px solid #888888;
  min-width: 60px;
  min-height: 24px;
  box-shadow: none;
}
.segmented-control button:last-child {
  border-right: none;
}
.segmented-control button.active, .segmented-control button[aria-pressed=true] {
  background: var(--button-blue-gradient);
  color: white;
  text-shadow: 0 -1px 0 rgba(0, 0, 0, 0.3);
}

/**
 * Tooltip
 * Mac OS X Aqua-style tooltips - JavaFX AquaFX accurate
 * Supports both data-attribute and nested element approaches
 */
/* Data-attribute tooltip (convenient) */
.tooltip,
[data-tooltip] {
  position: relative;
}
.tooltip::after,
[data-tooltip]::after {
  content: attr(data-tooltip);
  position: absolute;
  bottom: 100%;
  left: 50%;
  transform: translateX(-50%);
  padding: 1px 5px 3px 3px;
  background: var(--tooltip-bg);
  border: 0.5px solid var(--tooltip-border);
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  color: #000;
  white-space: nowrap;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.4);
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.15s, visibility 0.15s;
  pointer-events: none;
  z-index: 1000;
  margin-bottom: 6px;
}
.tooltip:hover::after,
[data-tooltip]:hover::after {
  opacity: 1;
  visibility: visible;
}

/* Class-based tooltip with nested element */
.tooltip-wrapper,
.aqua-tooltip {
  position: relative;
  display: inline-block;
}
.tooltip-wrapper .tooltip-text,
.aqua-tooltip .tooltip-text {
  visibility: hidden;
  opacity: 0;
  background: var(--tooltip-bg);
  color: #000;
  padding: 1px 5px 3px 3px;
  border: 0.5px solid var(--tooltip-border);
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  position: absolute;
  bottom: 125%;
  left: 50%;
  transform: translateX(-50%);
  white-space: nowrap;
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.4);
  z-index: 1000;
  transition: opacity 0.15s, visibility 0.15s;
}
.tooltip-wrapper:hover .tooltip-text,
.aqua-tooltip:hover .tooltip-text {
  visibility: visible;
  opacity: 1;
}

/* Tooltip positions */
.tooltip.top::after,
[data-tooltip].top::after {
  bottom: 100%;
  top: auto;
  margin-bottom: 6px;
}

.tooltip.bottom::after,
[data-tooltip].bottom::after {
  bottom: auto;
  top: 100%;
  margin-top: 6px;
  margin-bottom: 0;
}

.tooltip.left::after,
[data-tooltip].left::after {
  left: auto;
  right: 100%;
  bottom: auto;
  top: 50%;
  transform: translateY(-50%);
  margin-right: 6px;
  margin-bottom: 0;
}

.tooltip.right::after,
[data-tooltip].right::after {
  left: 100%;
  right: auto;
  bottom: auto;
  top: 50%;
  transform: translateY(-50%);
  margin-left: 6px;
  margin-bottom: 0;
}

/**
 * Balloon / Speech Bubble
 * Tooltip with a speech bubble tail
 */
.balloon,
[role=tooltip].balloon {
  position: relative;
  padding: 12px 16px;
  background: linear-gradient(180deg, #FFFFEE 0%, #FFFFC8 100%);
  border: 1px solid #999999;
  border-radius: var(--radius-medium);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  font-size: var(--font-size-base);
  /* Tail pointing up (default) */
}
.balloon::before,
[role=tooltip].balloon::before {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #999999;
  top: -9px;
  left: 16px;
}
.balloon::after,
[role=tooltip].balloon::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  border-bottom: 7px solid #FFFFEE;
  top: -7px;
  left: 17px;
}
.balloon,
[role=tooltip].balloon {
  /* Tail pointing down */
}
.balloon.balloon-bottom::before,
[role=tooltip].balloon.balloon-bottom::before {
  border-bottom: none;
  border-top: 8px solid #999999;
  top: auto;
  bottom: -9px;
}
.balloon.balloon-bottom::after,
[role=tooltip].balloon.balloon-bottom::after {
  border-bottom: none;
  border-top: 7px solid #FFFFC8;
  top: auto;
  bottom: -7px;
}
.balloon,
[role=tooltip].balloon {
  /* Tail on the right side */
}
.balloon.balloon-right::before, .balloon.balloon-right::after,
[role=tooltip].balloon.balloon-right::before,
[role=tooltip].balloon.balloon-right::after {
  left: auto;
  right: 16px;
}
.balloon.balloon-right::after,
[role=tooltip].balloon.balloon-right::after {
  right: 17px;
}
.balloon,
[role=tooltip].balloon {
  /* Tail pointing left */
}
.balloon.balloon-left-tail::before,
[role=tooltip].balloon.balloon-left-tail::before {
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-left: none;
  border-right: 8px solid #999999;
  top: 12px;
  left: -9px;
}
.balloon.balloon-left-tail::after,
[role=tooltip].balloon.balloon-left-tail::after {
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-left: none;
  border-right: 7px solid #FFFFEE;
  top: 13px;
  left: -7px;
}
.balloon,
[role=tooltip].balloon {
  /* Tail pointing right */
}
.balloon.balloon-right-tail::before,
[role=tooltip].balloon.balloon-right-tail::before {
  border-top: 8px solid transparent;
  border-bottom: 8px solid transparent;
  border-right: none;
  border-left: 8px solid #999999;
  top: 12px;
  left: auto;
  right: -9px;
}
.balloon.balloon-right-tail::after,
[role=tooltip].balloon.balloon-right-tail::after {
  border-top: 7px solid transparent;
  border-bottom: 7px solid transparent;
  border-right: none;
  border-left: 7px solid #FFFFC8;
  top: 13px;
  left: auto;
  right: -7px;
}

/**
 * Stepper / Number Input
 * Number input with increment/decrement buttons
 */
.stepper {
  display: inline-flex;
  align-items: stretch;
  border: 1px solid rgb(152, 152, 152);
  border-radius: 4px;
  overflow: hidden;
  background: white;
}
.stepper input[type=number] {
  border: none;
  border-radius: 0;
  box-shadow: none;
  width: 50px;
  height: auto;
  padding: 4px 8px;
  text-align: center;
  background: white;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
  -moz-appearance: textfield;
}
.stepper input[type=number]::-webkit-inner-spin-button, .stepper input[type=number]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.stepper input[type=number]:focus {
  outline: none;
}
.stepper button {
  min-width: 28px;
  padding: 4px 8px;
  border: none;
  border-radius: 0;
  font-size: 14px;
  font-weight: 500;
  line-height: 1;
  background: linear-gradient(rgb(255, 255, 255) 0%, rgb(251, 251, 251) 25%, rgb(243, 243, 243) 50%, rgb(236, 236, 236) 51%, rgb(243, 243, 243) 100%);
  cursor: default;
  color: #333;
  position: relative;
  overflow: hidden;
  /* Aqua glass effect */
}
.stepper button::before {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  top: 5%;
  background: linear-gradient(rgba(255, 255, 255, 0.9), rgba(255, 255, 255, 0.3));
  border-radius: inherit;
  filter: blur(1px);
  z-index: 1;
  pointer-events: none;
}
.stepper button::after {
  content: "";
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  height: 33%;
  width: 90%;
  bottom: 10%;
  background: linear-gradient(rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.5));
  border-radius: inherit;
  filter: blur(3px);
  z-index: 1;
  pointer-events: none;
}
.stepper button:first-child {
  border-right: 1px solid rgb(152, 152, 152);
}
.stepper button:last-child {
  border-left: 1px solid rgb(152, 152, 152);
}
.stepper button:hover {
  background: linear-gradient(rgb(245, 245, 245) 0%, rgb(240, 240, 240) 25%, rgb(235, 235, 235) 50%, rgb(230, 230, 230) 51%, rgb(238, 238, 238) 100%);
}
.stepper button:active {
  background: linear-gradient(rgb(176, 205, 236) 0%, rgb(87, 143, 230) 50%, rgb(158, 203, 236) 100%);
}

/* ============================================
   Aqua Spinner / Stepper (JavaFX-style)
   ============================================ */
.aqua-spinner {
  display: inline-flex;
  align-items: stretch;
  font-family: var(--font-system);
  font-size: var(--font-size-base);
}
.aqua-spinner input {
  width: 60px;
  height: auto;
  padding: 4px 8px;
  text-align: center;
  border: 1px solid rgb(168, 168, 168);
  border-right: none;
  border-radius: 3px 0 0 3px;
  outline: none;
  background: white;
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1);
}
.aqua-spinner input::-webkit-inner-spin-button, .aqua-spinner input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.aqua-spinner input {
  -moz-appearance: textfield;
}
.aqua-spinner input:focus {
  border-color: rgb(104, 155, 201);
  box-shadow: inset 0 1px 2px rgba(0, 0, 0, 0.1), 0 0 0 2px rgba(104, 155, 201, 0.4);
}

.aqua-spinner-buttons {
