.theme-graphite .segmented-control button.active {
  background: var(--button-blue-gradient);
}
.theme-graphite .aqua-list.focused .aqua-list-item.selected,
.theme-graphite .aqua-table tr.selected td {
  background: linear-gradient(rgb(180, 180, 180), rgb(140, 140, 140));
}
.theme-graphite .menu-bar .menu-title:hover,
.theme-graphite .menu-bar .menu-dropdown li:hover {
  background: var(--aqua-blue);
}
.theme-graphite .aqua-tab.active {
  background: linear-gradient(rgb(180, 180, 180), rgb(140, 140, 140));
}
.theme-graphite .aqua-pagination-btn.active {
  background: linear-gradient(rgb(180, 180, 180), rgb(140, 140, 140));
